<template>
  <!-- 搜索菜单部分的过渡效果 -->
  <transition name="fade">
    <div class="table-com-search" v-show="showSearch">
      <el-row :gutter="20">
        <el-col :span="6">
          <el-form-item label="主体">
            <el-select v-model="searchForm.subject_id" placeholder="选择主体" clearable filterable>
              <el-option label="全部" :value="''" />
              <el-option 
                v-for="item in subjectList" 
                :key="item.id" 
                :label="item.name" 
                :value="item.id" 
              />
            </el-select>
          </el-form-item>
        </el-col>
        <!-- 代理商由后端自动过滤，系统管理员可搜索，代理商自动过滤自己的数据 -->
        <el-col :span="6" v-if="isSystemAdmin">
          <el-form-item label="代理商ID">
            <el-input 
              v-model="searchForm.agent_id" 
              placeholder="按代理商ID搜索" 
              clearable 
              type="number"
            />
          </el-form-item>
        </el-col>
        <el-col :span="6">
          <el-form-item label="投诉单号">
            <el-input 
              v-model="searchForm.complaint_no" 
              placeholder="按投诉单号搜索" 
              clearable 
            />
          </el-form-item>
        </el-col>
        <el-col :span="6">
          <el-form-item label="状态">
            <el-select v-model="searchForm.status" placeholder="选择状态" clearable>
              <el-option label="全部" :value="''" />
              <el-option label="待处理" :value="'WAIT_PROCESS'" />
              <el-option label="处理中" :value="'PROCESSING'" />
              <el-option label="处理完成" :value="'PROCESSED'" />
              <el-option label="部分超时未处理" :value="'PART_OVERDUE'" />
              <el-option label="超时未处理" :value="'OVERDUE'" />
              <el-option label="超时处理完成" :value="'OVERDUE_PROCESSED'" />
              <el-option label="用户撤诉" :value="'DROP_COMPLAIN'" />
              <el-option label="处理完成用户撤诉" :value="'DROP_PROCESSED'" />
              <el-option label="超时后用户撤诉" :value="'DROP_OVERDUE_COMPLAIN'" />
              <el-option label="超时处理完成用户撤诉" :value="'DROP_OVERDUE_PROCESSED'" />
            </el-select>
          </el-form-item>
        </el-col>
      </el-row>
      
      <el-row :gutter="20">
        <el-col :span="12">
          <el-form-item label="投诉时间">
            <el-date-picker
              v-model="dateRange"
              type="datetimerange"
              range-separator="至"
              start-placeholder="开始时间"
              end-placeholder="结束时间"
              format="YYYY-MM-DD HH:mm:ss"
              value-format="YYYY-MM-DD HH:mm:ss"
              @change="handleDateChange"
            />
          </el-form-item>
        </el-col>
      </el-row>

      <!-- 搜索按钮和重置按钮 -->
      <el-form :inline="true" class="demo-form-inline">
        <el-form-item>
          <el-button type="primary" @click="handleSearch">搜索</el-button>
          <el-button type="primary" @click="handleReset">重置</el-button>
        </el-form-item>
      </el-form>
    </div>
  </transition>

  <!-- 表头 -->
  <div class="table-header ba-scroll-style">
    <!-- 刷新按钮 -->
    <el-button :icon="Refresh" @click="refreshData" color="#40485b"/>

    <!-- 搜索按钮 -->
    <div class="table-search">
      <el-button-group class="ml-4">
        <el-button :icon="Search" @click="toggleSearch" />
      </el-button-group>
    </div>
  </div>

  <!-- 表格内容 -->
  <div style="width: 100%; overflow-x: auto;" class="table-content">
    <el-table
        :data="data.data"
        border
        stripe
        max-height="600"
        style="width: 100%;">
      <el-table-column prop="id" label="ID" align="center" min-width="80"/>
      <el-table-column prop="complaint_no" label="投诉单号" align="center" min-width="200" show-overflow-tooltip/>
      <el-table-column prop="subject_name" label="主体名称" align="center" min-width="180" show-overflow-tooltip/>
      <el-table-column v-if="isSystemAdmin" prop="agent_name" label="代理商" align="center" min-width="150"/>
      
      <!-- 投诉金额列 -->
      <el-table-column label="投诉金额" align="center" min-width="120">
        <template #default="{ row }">
          {{ row.complaint_amount > 0 ? `¥${row.complaint_amount}` : '-' }}
        </template>
      </el-table-column>

      <!-- 已退款金额列 -->
      <el-table-column label="已退款" align="center" min-width="120">
        <template #default="{ row }">
          <el-tag v-if="row.refund_amount > 0" type="success">¥{{ row.refund_amount }}</el-tag>
          <span v-else style="color: #999;">-</span>
        </template>
      </el-table-column>
      
      <el-table-column prop="complainant_id" label="投诉人ID" align="center" min-width="180" show-overflow-tooltip/>
      <el-table-column prop="complaint_time" label="投诉时间" align="center" min-width="180"/>
      
      <!-- 状态列 -->
      <el-table-column label="状态" align="center" min-width="150">
        <template #default="{ row }">
          <el-tag :type="getStatusType(row.complaint_status)">
            {{ getStatusLabel(row.complaint_status) }}
          </el-tag>
        </template>
      </el-table-column>

      <el-table-column prop="created_at" label="创建时间" align="center" min-width="180"/>

      <!-- 操作列 -->
      <el-table-column label="操作" align="center" fixed="right" min-width="120">
        <template #default="scope">
          <el-button size="small" type="primary" @click="handleComplaint(scope.row)">处理投诉</el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>

  <!-- 表格分页 -->
  <div class="table-footer">
    <div class="table-pagination">
      <el-pagination
          v-model:current-page="data.current_page"
          v-model:page-size="data.page_size"
          :page-sizes="[10, 50, 100, 200]"
          :background="true"
          prev-text="上一页"
          next-text="下一页"
          layout="total, sizes, prev, pager, next, jumper"
          :total="data.page_total"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
      />
    </div>
  </div>

  <!-- 投诉处理对话框 -->
  <el-dialog v-model="handleDialogVisible" title="处理投诉" width="90%" :close-on-click-modal="false">
    <div v-if="currentComplaint" class="complaint-handle">
      <!-- 投诉基本信息 -->
      <el-card class="box-card" shadow="never" style="margin-bottom: 20px;">
        <template #header>
          <div class="card-header">
            <span>投诉基本信息</span>
          </div>
        </template>
        <el-descriptions :column="2" border>
          <el-descriptions-item label="投诉单号">{{ currentComplaint.complaint_no }}</el-descriptions-item>
          <el-descriptions-item label="主体名称">{{ currentComplaint.subject_name }}</el-descriptions-item>
          <el-descriptions-item label="投诉人ID">{{ currentComplaint.complainant_id }}</el-descriptions-item>
          <el-descriptions-item label="投诉时间">{{ currentComplaint.complaint_time }}</el-descriptions-item>
          <el-descriptions-item label="投诉金额">
            <span style="color: #f56c6c; font-weight: bold;">
              {{ currentComplaint?.complaint_amount > 0 ? `¥${currentComplaint.complaint_amount}` : '-' }}
            </span>
          </el-descriptions-item>
          <el-descriptions-item label="当前状态">
            <el-tag :type="getStatusType(currentComplaint.complaint_status)">
              {{ getStatusLabel(currentComplaint.complaint_status) }}
            </el-tag>
          </el-descriptions-item>
          <el-descriptions-item label="投诉原因" :span="2">{{ currentComplaint.complaint_reason }}</el-descriptions-item>
        </el-descriptions>
      </el-card>

      <!-- 投诉订单列表（仅待处理和处理中状态显示） -->
      <el-card 
        v-if="currentComplaint?.complaint_status === 'WAIT_PROCESS' || currentComplaint?.complaint_status === 'PROCESSING'"
        class="box-card" 
        shadow="never"
        style="margin-bottom: 20px;">
        <template #header>
          <div class="card-header">
            <span>投诉订单列表</span>
            <span style="margin-left: 10px; color: #999; font-size: 12px;">
              （请选择需要处理的订单，如不选择则处理所有订单）
            </span>
          </div>
        </template>
        <el-table
          :data="currentComplaint?.details || []"
          border
          stripe
          max-height="300"
          @selection-change="handleSelectionChange">
          <el-table-column type="selection" width="55" align="center" />
          <el-table-column prop="id" label="ID" align="center" width="80" />
          <el-table-column prop="merchant_order_no" label="商户订单号" align="center" min-width="200" show-overflow-tooltip />
          <el-table-column prop="platform_order_no" label="平台订单号" align="center" min-width="200" show-overflow-tooltip />
          <el-table-column prop="order_amount" label="订单金额" align="center" width="120">
            <template #default="{ row }">
              ¥{{ row.order_amount || '0.00' }}
            </template>
          </el-table-column>
          <el-table-column prop="complaint_amount" label="投诉金额" align="center" width="120">
            <template #default="{ row }">
              <span style="color: #f56c6c; font-weight: bold;">
                ¥{{ row.complaint_amount || '0.00' }}
              </span>
            </template>
          </el-table-column>
        </el-table>
      </el-card>

      <!-- 商家反馈处理表单（仅待处理和处理中状态显示） -->
      <el-card 
        v-if="currentComplaint?.complaint_status === 'WAIT_PROCESS' || currentComplaint?.complaint_status === 'PROCESSING'"
        class="box-card" 
        shadow="never">
        <template #header>
          <div class="card-header">
            <span>商家反馈处理</span>
          </div>
        </template>
        <el-form :model="handleForm" ref="handleFormRef" label-width="130px" :rules="formRules">
          <el-form-item label="当前状态">
            <el-tag :type="getStatusType(currentComplaint?.complaint_status)">
              {{ getStatusLabel(currentComplaint?.complaint_status) }}
            </el-tag>
          </el-form-item>

          <el-form-item label="处理结果码" prop="process_code">
            <el-select v-model="handleForm.process_code" placeholder="请选择处理结果码" style="width: 100%;">
              <el-option label="已联系到用户，协商一致，无异议" value="CONSENSUS_WITH_CLIENT" />
              <el-option label="已退款，用户无异议" value="REFUND" />
              <el-option label="不涉及退款，已针对投诉内容进行整改" value="RECTIFICATION_NO_REFUND" />
              <el-option label="已提交证明材料" value="SUBMIT_PROOF_NOT_CONTACTED" />
              <el-option label="其他渠道已退款" value="OTHER_CHANNEL_REFUND" />
              <el-option label="其他" value="ORTHER" />
            </el-select>
          </el-form-item>

          <el-form-item label="反馈内容（选填）" prop="feedback">
            <el-input
              v-model="handleForm.feedback"
              type="textarea"
              :rows="5"
              placeholder="请详细描述您对该投诉的处理情况和解决方案（可选）..."
              maxlength="500"
              show-word-limit
            />
          </el-form-item>

          <el-form-item label="退款金额" prop="refund_amount">
            <el-input-number
              v-model="handleForm.refund_amount"
              :min="0"
              :max="currentComplaint?.complaint_amount || 999999.99"
              :precision="2"
              :step="0.01"
              placeholder="如有退款请填写"
              style="width: 200px;"
            />
            <span v-if="currentComplaint?.complaint_amount > 0" style="margin-left: 10px; color: #999; font-size: 12px;">
              最大可退款金额：¥{{ currentComplaint.complaint_amount }}
            </span>
          </el-form-item>
        </el-form>
      </el-card>
    </div>

    <template #footer>
      <el-button @click="handleDialogVisible = false">取消</el-button>
      <el-button type="primary" @click="submitHandle" :loading="submitting">提交处理</el-button>
    </template>
  </el-dialog>
</template>

<script lang="ts" setup>
import { Refresh, Search } from '@element-plus/icons-vue';
import { ref, onMounted, computed } from 'vue';
import request from '@/utils/request';
import { ElMessage } from 'element-plus';

// 引入自定义插件和 API
import { useTableDataPlugin } from "@/utils/plugins/table.plugin.ts";

// 使用表格插件来管理表格的状态、数据等
const {
  isVisible: showSearch,
  toggleVisibility: toggleSearch,
  loadData,
  data,
  refreshData,
  handleSizeChange,
  handleCurrentChange,
  searchParams
} = useTableDataPlugin("/api/v1/admin/complaint/list");

// 搜索表单数据
const searchForm = ref({
  subject_id: '',
  agent_id: '',
  complaint_no: '',
  status: '',
  start_time: '',
  end_time: ''
});

// 日期范围
const dateRange = ref<[string, string] | null>(null);

// 主体列表
const subjectList = ref<any[]>([]);

// 投诉处理对话框
const handleDialogVisible = ref(false);
const currentComplaint = ref<any>(null);
const handleFormRef = ref<any>(null);
const submitting = ref(false);

// 处理表单数据
const handleForm = ref({
  process_code: '',
  feedback: '',
  refund_amount: 0
});

// 选中的订单ID列表
const selectedDetailIds = ref<number[]>([]);

// 处理订单选择变化
const handleSelectionChange = (selection: any[]) => {
  selectedDetailIds.value = selection.map((item: any) => item.id);
};

// 表单验证规则
const formRules = {
  process_code: [
    { required: true, message: '请选择处理结果码', trigger: 'change' }
  ],
  feedback: [
    // 反馈内容改为选填，不再验证
  ]
};

// 用户信息
const userInfo = ref<any>({});
const isAgent = computed(() => {
  return userInfo.value?.is_agent === true;
});
// 判断是否是系统管理员（user_group_id === 1）
const isSystemAdmin = computed(() => {
  return userInfo.value?.user_group_id === 1;
});

// 处理日期变化
const handleDateChange = (value: [string, string] | null) => {
  if (value) {
    searchForm.value.start_time = value[0];
    searchForm.value.end_time = value[1];
  } else {
    searchForm.value.start_time = '';
    searchForm.value.end_time = '';
  }
};

// 处理搜索
const handleSearch = async () => {
  // 将搜索表单数据同步到searchParams
  searchParams.value = { ...searchForm.value };
  await refreshData();
};

// 处理重置
const handleReset = () => {
  searchForm.value = {
    subject_id: '',
    agent_id: '',
    complaint_no: '',
    status: '',
    start_time: '',
    end_time: ''
  };
  dateRange.value = null;
  searchParams.value = { ...searchForm.value };
  refreshData();
};

// 获取主体列表
const getSubjectList = async () => {
  try {
    const res = await request({
      url: '/api/v1/admin/complaint/subject-list',
      method: 'GET'
    });
    if (res.status) {
      subjectList.value = res.data;
    }
  } catch (error) {
    console.error('获取主体列表失败:', error);
  }
};

// 处理投诉
const handleComplaint = async (row: any) => {
  try {
    const res = await request({
      url: '/api/v1/admin/complaint/detail',
      method: 'GET',
      params: {
        id: row.id
      }
    });
    if (res.status) {
      currentComplaint.value = res.data;
      // 重置表单
      handleForm.value = {
        process_code: '',
        feedback: '',
        refund_amount: 0
      };
      // 重置选中的订单
      selectedDetailIds.value = [];
      handleDialogVisible.value = true;
    } else {
      ElMessage.error(res.message || '获取详情失败');
    }
  } catch (error: any) {
    console.error('获取投诉详情失败:', error);
    ElMessage.error(error.message || '获取详情失败');
  }
};

// 提交处理
const submitHandle = async () => {
  if (!handleFormRef.value) return;
  
  await handleFormRef.value.validate(async (valid: boolean) => {
    if (!valid) return;
    
    submitting.value = true;
    try {
      // 如果选择了订单，只处理选中的订单；否则处理所有订单
      const requestData: any = {
        id: currentComplaint.value.id,
        process_code: handleForm.value.process_code,
        feedback: handleForm.value.feedback,
        refund_amount: handleForm.value.refund_amount
      };
      
      // 如果选择了订单，传递选中的订单ID列表
      if (selectedDetailIds.value.length > 0) {
        requestData.detail_ids = selectedDetailIds.value;
      }
      
      const res = await request({
        url: '/api/v1/admin/complaint/handle',
        method: 'POST',
        data: requestData
      });
      
      if (res.status) {
        ElMessage.success('处理成功');
        handleDialogVisible.value = false;
        refreshData();
      } else {
        ElMessage.error(res.message || '处理失败');
      }
    } catch (error: any) {
      console.error('处理投诉失败:', error);
      ElMessage.error(error.message || '处理失败');
    } finally {
      submitting.value = false;
    }
  });
};

// 获取状态标签
const getStatusLabel = (status: string) => {
  const statusMap: Record<string, string> = {
    // 标准支付宝投诉状态
    'WAIT_PROCESS': '待处理',
    'PROCESSING': '处理中',
    'PROCESSED': '处理完成',
    'OVERDUE': '超时未处理',
    'OVERDUE_PROCESSED': '超时处理完成',
    'PART_OVERDUE': '部分超时未处理',
    'DROP_COMPLAIN': '用户撤诉',
    'DROP_PROCESSED': '处理完成用户撤诉',
    'DROP_OVERDUE_COMPLAIN': '超时后用户撤诉',
    'DROP_OVERDUE_PROCESSED': '超时处理完成用户撤诉',
    // 其他可能状态
    'CLOSED': '已关闭',
    'MERCHANT_PROCESSING': '商家处理中',
    'MERCHANT_FEEDBACKED': '商家已反馈',
    'SUCCESS': '投诉完结',
    'CANCELLED': '投诉撤销',
    'FINISHED': '已完结',
    'MERCHANT_REPLIED': '商家已回复',
    '待处理': '待处理',
    '处理中': '处理中',
    '处理完成': '处理完成'
  };
  return statusMap[status] || status;
};

// 获取状态类型
const getStatusType = (status: string) => {
  const typeMap: Record<string, string> = {
    'WAIT_PROCESS': 'danger',
    'PROCESSING': 'warning',
    'PROCESSED': 'success',
    'OVERDUE': 'danger',
    'OVERDUE_PROCESSED': 'warning',
    'PART_OVERDUE': 'warning',
    'DROP_COMPLAIN': 'info',
    'DROP_PROCESSED': 'info',
    'DROP_OVERDUE_COMPLAIN': 'info',
    'DROP_OVERDUE_PROCESSED': 'info',
    'CLOSED': 'info'
  };
  return typeMap[status] || 'info';
};

// 获取用户信息
const getUserInfo = async () => {
  try {
    const res = await request({
      url: '/api/v1/admin/user/info',
      method: 'GET'
    });
    if (res.status) {
      userInfo.value = res.data;
    }
  } catch (error) {
    console.error('获取用户信息失败:', error);
  }
};

// 在组件挂载时加载数据
onMounted(() => {
  getUserInfo();
  getSubjectList();
  loadData();
});

</script>

<style scoped>
.complaint-detail {
  padding: 10px;
}

.complaint-detail h3 {
  font-size: 16px;
  font-weight: bold;
  margin: 10px 0;
}
</style>

